body {
  margin: 0;
}

.stopwatch {
  justify-content: center;
  align-items: center;
  height: 100vh;
  display: flex;
}

.stopwatch__circle {
  box-sizing: border-box;
  border: 8px dotted #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 80vmin;
  height: 80vmin;
  display: flex;
  position: relative;
}

.stopwatch__minute-hand {
  transform-origin: bottom;
  background-color: green;
  border-radius: 15px;
  width: 10px;
  height: 50%;
  animation: 5s linear infinite rotate-hands;
  position: absolute;
  top: 0;
}

.stopwatch__second-hand {
  transform-origin: bottom;
  background-color: #00f;
  border-radius: 15px;
  width: 10px;
  height: 25%;
  animation: 60s linear infinite rotate-hands;
  position: absolute;
  top: 25%;
}

.stopwatch__inner-circle {
  background-color: orange;
  border-radius: 50%;
  width: 4%;
  height: 4%;
  position: absolute;
}

@keyframes rotate-hands {
  from {
    transform: rotate(0);
  }

  to {
    transform: rotate(360deg);
  }
}

